<template>
 <div>
    <el-collapse v-model="activeNames" @change="handleChange" accordion>
      <el-collapse-item v-for="(item,index) in CommandList" :key="index" :name="index">
        <template slot="title">
            <el-alert
              type="primary"
              effect="dark"
              :closable="false"
              >
                <template slot="title">
                    <p style="font-size: 30px;font-family:'';">{{item.name}}</p>
                </template>
            </el-alert>
        </template>
        <div style="font-size: 50px;font-family:'';">{{item.desc}}</div>
      </el-collapse-item>
    </el-collapse>
  </div>
</template>

<script>
export default {
  name: "DiscordBOT",
  data() {
    return {
      CommandList:[
        {name:'+[Command]',desc:'',visable:true},
        {name:'![ParentItem] [ChildItem]',desc:'',visable:false},
        {name:'-[ParentItem] [ChildItem]',desc:'',visable:true},
        {name:'*[Command]',desc:'',visable:true},
        {name:'+[Command]',desc:'',visable:true},
        {name:'#[Setting]',desc:'',visable:true},
        {name:'%[ParentItem] [Index] [NewWeight]',desc:'',visable:true},
      ],
      activeNames: ['1']
    }
  },
  methods: {
    handleChange(){

    }
  },
  mounted(){

  }
}
</script>

<style>
.el-collapse-item__header{
  height:100px;
}
</style>




