<template>
<!-- Wrapper -->
			<div id="wrapper">
				<!-- Header -->
					<header id="header">
						<div class="inner">
							<!-- Logo -->
								<a href="index.html" class="logo">
									<span class="symbol"><img src="../assets/Images/logo.svg" alt="" /></span><span class="title">Phantom</span>
								</a>
							<!-- Nav -->
