									<ul>
                    	<el-button @click="menuClick" icon="el-icon-more"></el-button>
									</ul>
								</nav>
						</div>
					</header>
				<!-- Menu -->
				<nav id="menu">
          <div class="inner">
              <h2>Menu</h2>
              <ul>
                <li v-for="(item,index) in menuList" :key="index">
                  <a :href="item.href">{{item.name}}</a>
                </li>
              </ul>
          </div>
          <a class="close" href="#menu">Close</a>
				</nav>

				<!-- Main -->
				<div id="main">
					<div class="inner">
						<header>
							<h1><br />
							template designed by <a href="http://html5up.net">HTML5 UP</a>.</h1>
							<!-- <p>Etiam quis viverra lorem, in semper lorem. Sed nisl arcu euismod sit amet nisi euismod sed cursus arcu elementum ipsum arcu vivamus quis venenatis orci lorem ipsum et magna feugiat veroeros aliquam. Lorem ipsum dolor sit amet nullam dolore.</p> -->
						</header>
						<section class="tiles">
							<article v-for="(item,index) in cardList" :key="index" :class="'style'+ index">
								<span class="image">
									<img :src="require(`../assets/Images/${item.picture}.jpg`)" :alt="item.router" />
								</span>
								<a href="generic.html">
									<h2>{{item.name}}</h2>
									<div class="content">
										<p>{{item.desc}}</p>
									</div>
								</a>
							</article>
						</section>
					</div>
				</div>

				<!-- Footer -->
				<footer id="footer">
					<div class="inner">
						<section>
							<h2>Get in touch</h2>
							<form method="post" action="#">
								<div class="fields">
									<div class="field half">
										<input type="text" name="name" id="name" placeholder="Name" />
									</div>
									<div class="field half">
										<input type="email" name="email" id="email" placeholder="Email" />
									</div>
									<div class="field">
										<textarea name="message" id="message" placeholder="Message"></textarea>
									</div>
								</div>
								<ul class="actions">
									<li><input type="submit" value="Send" class="primary" /></li>
								</ul>
							</form>
						</section>
						<section>
							<h2>Follow</h2>
							<ul class="icons">
								<li v-for="(item,index) in socialMediaList" :key="index" >
									<a :href="item.href" :class="item.icon">
										<span class="label">{{item.name}}</span>
									</a>
								</li>
							</ul>
						</section>
						<ul class="copyright">
							<li>&copy; Untitled. All rights reserved</li><li>Design: <a href="http://html5up.net">HTML5 UP</a></li>
						</ul>
					</div>
				</footer>
			</div>
</template>

<script>
export default {
  name: "MainPage",
  data() {
    return {
      cardList: [
        { name: "", showdow: "hover", router:"PMS",desc:"",icon:"el-icon-document",color:'',IsShow:true,picture:'pic01'},
        { name: "", showdow: "hover", router:"Import", desc:"",icon:"el-icon-upload2",color:'',IsShow:true,picture:'pic02'},
        { name: "", showdow: "hover", router:"Point",desc:"",icon:"el-icon-top-right",color:'',IsShow:true,picture:'pic03'},
        { name: "ORTool", showdow: "hover", router:"ORTool",desc:"ORTool",icon:"el-icon-finished",color:'',IsShow:true,picture:'pic04'},
        { name: "API", showdow: "hover", router:"API", desc:"API",icon:"el-icon-connection",color:'',IsShow:true,picture:'pic05'},
        { name: "Sample", showdow: "hover", router:"Sample", desc:"Sample",icon:"el-icon-connection",color:'',IsShow:true,picture:'pic06'},
        { name: "", showdow: "hover", router:"ExchangeRate", desc:"ExchangeRate",icon:"el-icon-coin",color:'',IsShow:false,picture:'pic07'},
        { name: "DiscordBOT", showdow: "hover", router:"DiscordBOT", desc:"DiscordBOT",icon:"el-icon-platform-eleme",color:'',IsShow:true,picture:'pic08'},
      ],
      socialMediaList:[
        {name:'Twitter',href:'#',icon:'icon brands style2 fa-twitter'},
        {name:'Facebook',href:'#',icon:'icon brands style2 fa-facebook-f'},
        {name:'Instagram',href:'#',icon:'icon brands style2 fa-instagram'},
        {name:'Dribbble',href:'#',icon:'icon brands style2 fa-dribbble'},
        {name:'GitHub',href:'#',icon:'icon brands style2 fa-github'},
        {name:'500px',href:'#',icon:'icon brands style2 fa-500px'},
        {name:'Phone',href:'#',icon:'icon solid style2 fa-phone'},
        {name:'Email',href:'#',icon:'icon solid style2 fa-envelope'}
      ],
      menuList:[
        {name:'Home',href:'index.html'},
        {name:'Ipsum veroeros',href:'generic.html'},
        {name:'Tempus etiam',href:'generic.html'},
        {name:'Consequat dolor',href:'generic.html'},
        {name:'Elements',href:'elements.html'},
	  ],
	  menuShowFlag:false
    };
  },
  methods:{
    menuClick(e){
	   if(this.menuShowFlag){
			this.menuShowFlag = false;
			document.body.classList.classList.remove("is-menu-visible");
	   }
	   else{
			document.body.classList.add("is-menu-visible");
			this.menuShowFlag = true;
	   }
      //document.body.toggleClass('is-menu-visible');
    }
  },
  mounted(){

  },
  components:{

  }
};
</script>

<style scoped src="../assets/MainPage/css/noscript.css"></style>
<style scoped src="../assets/MainPage/css/main.css"></style>



